import React from 'react';

const Slider = ({ min, max, step, value, onValueChange }) => {
  return (
    <input
      type="range"
      min={min}
      max={max}
      step={step}
      value={value}
      onChange={(e) => onValueChange([e.target.valueAsNumber])}
    />
  );
};

export default Slider;