import React, { useState } from 'react';
import Card, { CardHeader, CardContent } from './components/ui/card';
import Button from './components/ui/button';
import Checkbox from './components/ui/checkbox';
import Slider from './components/ui/slider';

const DatingFilter = () => {
  const [filters, setFilters] = useState({
    heightRange: [160, 180],
    weightRange: [50, 70],
    genders: [],
    ageRange: [25, 35],
    minIncome: 50000,
    education: '',
    ethnicities: [],
  });
  const [result, setResult] = useState(null);
  const [prScore, setPrScore] = useState(null);

  const [userProbabilities, setUserProbabilities] = useState([]);
  const [totalUsers, setTotalUsers] = useState(0);
  const [averageProbability, setAverageProbability] = useState(0);
  const [stdDevProbability, setStdDevProbability] = useState(0);

  const handleChange = (name, value) => {
    setFilters(prev => ({ ...prev, [name]: value }));
  };

  const handleCheckboxChange = (name, value, checked) => {
    setFilters(prev => ({
      ...prev,
      [name]: checked 
        ? [...prev[name], value]
        : prev[name].filter(item => item !== value)
    }));
  };

  const calculateProbability = () => {
    let probability = 1;

    const heightRange = filters.heightRange[1] - filters.heightRange[0];
    const heightProb = Math.min(1, heightRange / 60);
    probability *= heightProb;

    const weightRange = filters.weightRange[1] - filters.weightRange[0];
    const weightProb = Math.min(1, weightRange / 50);
    probability *= weightProb;

    const genderProb = filters.genders.reduce((acc, gender) => {
      const genderProbabilities = {
        'male': 0.488,
        'female': 0.488,
        'transgender_male': 0.012,
        'transgender_female': 0.012
      };
      return acc + genderProbabilities[gender];
    }, 0);
    probability *= Math.min(1, genderProb);

    const ageRange = filters.ageRange[1] - filters.ageRange[0];
    const ageProb = Math.min(1, ageRange / 62);
    probability *= ageProb;

    const incomeProb = Math.max(0, 1 - (Math.log(filters.minIncome) - Math.log(30000)) / (Math.log(1000000) - Math.log(30000)));
    probability *= incomeProb;

    const educationProb = {
      'highschool': 0.6,
      'university': 0.25,
      'postgraduate': 0.05
    };
    const eduProb = filters.education ? educationProb[filters.education] : 1;
    probability *= eduProb;

    const ethnicityProb = filters.ethnicities.reduce((acc, ethnicity) => {
      const ethnicityProbabilities = {
        'east_asian': 0.21,
        'south_asian': 0.24,
        'southeast_asian': 0.08,
        'central_asian': 0.01,
        'white': 0.15,
        'black': 0.15,
        'hispanic': 0.09,
        'middle_eastern': 0.07
      };
      return acc + ethnicityProbabilities[ethnicity];
    }, 0);
    probability *= Math.min(1, ethnicityProb);

    const updatedProbabilities = [...userProbabilities, probability];
    const newTotalUsers = updatedProbabilities.length;
    const newAverageProbability = updatedProbabilities.reduce((acc, p) => acc + p, 0) / newTotalUsers;
    const newStdDevProbability = Math.sqrt(updatedProbabilities.reduce((acc, p) => acc + Math.pow(p - newAverageProbability, 2), 0) / newTotalUsers);

    setUserProbabilities(updatedProbabilities);
    setTotalUsers(newTotalUsers);
    setAverageProbability(newAverageProbability);
    setStdDevProbability(newStdDevProbability);

    const rank = updatedProbabilities.filter(p => p <= probability).length;
    const prScore = ((rank / newTotalUsers) * 100).toFixed(2);

    setResult((probability * 100).toFixed(4));
    setPrScore(prScore);
  };

  return (
    <Card className="w-full max-w-md mx-auto">
      <CardHeader>交往配對篩選器</CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div>
            <label>身高範圍 (cm): {filters.heightRange[0]} - {filters.heightRange[1]}</label>
            <Slider
              min={140}
              max={200}
              step={1}
              value={filters.heightRange}
              onValueChange={(value) => handleChange('heightRange', value)}
            />
          </div>
          <div>
            <label>體重範圍 (kg): {filters.weightRange[0]} - {filters.weightRange[1]}</label>
            <Slider
              min={40}
              max={120}
              step={1}
              value={filters.weightRange}
              onValueChange={(value) => handleChange('weightRange', value)}
            />
          </div>
          <div>
            <label>性別（可複選）:</label>
            {['male', 'female', 'transgender_male', 'transgender_female'].map((gender) => (
              <div key={gender}>
                <Checkbox
                  id={gender}
                  checked={filters.genders.includes(gender)}
                  onCheckedChange={(checked) => handleCheckboxChange('genders', gender, checked)}
                />
                <label htmlFor={gender}>
                  {gender === 'male' ? '男性' :
                   gender === 'female' ? '女性' :
                   gender === 'transgender_male' ? '跨性別男性' : '跨性別女性'}
                </label>
              </div>
            ))}
          </div>
          <div>
            <label>年齡範圍: {filters.ageRange[0]} - {filters.ageRange[1]}</label>
            <Slider
              min={18}
              max={80}
              step={1}
              value={filters.ageRange}
              onValueChange={(value) => handleChange('ageRange', value)}
            />
          </div>
          <div>
            <label>最低年收入 (美元): ${filters.minIncome}</label>
            <Slider
              min={30000}
              max={1000000}
              step={10000}
              value={[filters.minIncome]}
              onValueChange={([value]) => handleChange('minIncome', value)}
            />
          </div>
          <select 
            value={filters.education} 
            onChange={(e) => handleChange('education', e.target.value)}
          >
            <option value="">選擇教育程度</option>
            <option value="highschool">高中及以上</option>
            <option value="university">大學及以上</option>
            <option value="postgraduate">研究所及以上</option>
          </select>
          <div>
            <label>種族（可複選）:</label>
            {['east_asian', 'south_asian', 'southeast_asian', 'central_asian', 'white', 'black', 'hispanic', 'middle_eastern'].map((ethnicity) => (
              <div key={ethnicity}>
                <Checkbox
                  id={ethnicity}
                  checked={filters.ethnicities.includes(ethnicity)}
                  onCheckedChange={(checked) => handleCheckboxChange('ethnicities', ethnicity, checked)}
                />
                <label htmlFor={ethnicity}>
                  {ethnicity === 'east_asian' ? '東亞人' :
                   ethnicity === 'south_asian' ? '南亞人' :
                   ethnicity === 'southeast_asian' ? '東南亞人' :
                   ethnicity === 'central_asian' ? '中亞人' :
                   ethnicity === 'white' ? '白人' :
                   ethnicity === 'black' ? '黑人' :
                   ethnicity === 'hispanic' ? '拉丁美洲裔' : '中東和北非裔'}
                </label>
              </div>
            ))}
          </div>
          <Button onClick={calculateProbability}>計算機率</Button>
        </div>
        {result !== null && (
          <div className="mt-4">
            <p>你的理想對象在人口中的比例約為: {result}%</p>
            <p>你的擇偶條件PR值: {prScore}</p>
            <p>
              {prScore < 20
                ? "你的條件非常嚴格，找到完全符合條件的對象可能會很有挑戰性。考慮適當放寬一些要求可能會有幫助。"
                : prScore < 40
                ? "你的條件相對嚴格，可能需要多花些時間尋找理想對象。"
                : prScore < 60
                ? "你的條件適中，找到理想對象的難度適中。"
                : prScore < 80
                ? "你的條件相對寬鬆，有很多潛在的理想對象。"
                : "你的條件非常寬鬆，應該很容易找到合適的對象。"}
            </p>
          </div>
        )}
      </CardContent>
    </Card>
  );
};

export default DatingFilter;
